        {
            title: "Уже почти год встречаюсь с молодым человеком, он женат и верующий...",
            question: "«Помогите пожалуйста! Уже почти год встречаюсь с молодым человеком, он женат и верующий. Хочет развестись, но причина развода не я. У них с женой на протяжении многих лет нет понимания и идет коса на камень... Хотим быть вместе.»",
            answer: "Как же мы можем помочь вам в вашем вопросе? Исходя из письма ваше понимание семьи отличается от нашего понимания семьи. Библия нас учит, что проблемы во взаимоотношениях возникают по причине эгоизма , себялюбия, жестокосердия и решаются не разводом, а покаянием, прощением, любовью, терпением , миролюбием. \"Откуда у вас вражды и распри? Не отсюда ли, от вожделений ваших, воюющих в членах ваших\",- читаем в послании апостола Иакова 4-я глава 1-й стих. И Господь Иисус Христос говорит, что причиной развода является жестокосердие (Мф. 19:8) Так что тот молодой человек, с которым вы встречаетесь, и его жена должны перестать \"быть косой и камнем\", и не искать своего, а стремиться достигать любви и служения друг другу. Не желать, чтобы ему служили и угождали, а служить и угождать самому. Христос учит: \"И как хотите, чтобы с вами поступали люди, так вы поступайте с ними\".\n\nНачинать нужно с исправления своих путей и сердца. Теперь подумайте: хотели ли бы вы, чтобы ваш муж встречался с другой девушкой, будучи женатым на вас? Как бы вы себя чувствовали на месте той женщины, с мужем которой вы встречаетесь? Конечно можно обвинить ту женщину (что молодой человек и делает, хотя вы можете говорить, что он поступает благородно и не говорит о своей жене плохо). Но, что у них коса на камень, это вы знаете от него. Вы можете считать, что не являетесь причиной, того, что он хочет развестись (у них и без вас было плохо), но все-таки уже целый год вы принимаете время, сердце, чувства, мысли, в которых молодой человек отказал жене (а ведь обещал и они по праву принадлежат ей). Это мягко говоря не хорошо и неправильно. И по Библии неправильно и по житейски тоже. Если у них есть дети, то еще хуже: обворовываются и дети.\n\nДаже если у этого молодого человека в самом деле жена, с которой трудно жить, это не является причиной для развода (Мф.19:3-9). Любовь достигается, взаимоотношения строятся и чем больше смиряется человек, тем больше дается ему благодати от Бога. \"Бог гордым противится, а смиренным дает благодать\", - (1-е Петра 5:5; Иак.4:6; Притчи 11:2; Рим.12:16) Правильно будет покаяться перед Богом в неправильных действиях и желаниях и разорвать с молодым человеком (чужим мужем!) всякие отношения. Это будет трудно и очень больно, но правильно. Будете молиться Богу, - будете получать духовную помощь в этом вопросе. Даст Бог и у вас будет свой муж, своя семья, свое счастье. Но счастливым нужно уметь быть. И счастье в совместной жизни в семье требует усилий и труда, умения отдавать и принимать. Молодому человеку (христианину!?) нужно пойти к старейшинам (пастору) церкви, чтобы они помогли ему и его семье молитвами и добрыми советами. Можете дать прочитать ему наш ответ. В книге Библия есть такие замечательные слова: \"Выслушаем сущность всего: бойся Бога и заповеди Его соблюдай, потому что в этом все для человека. Ибо всякое дело Бог приведет на суд и все тайное, хорошо ли оно или худо.\" - Екклесиаст 12:13. Желаем вам Божьих благословений.",
            footer: "Господь Бог да благословит Вас.\nПресвитер церкви, Обровец П.Д."
        },
        {
            title: "Когда ремень не помогает, что делать?",
            question: "«Добрый день, подскажите. Ребенок 3 года, девочка - очень упрямая. Разбросать игрушки, еду для нее нет труда, понимает что это не хорошо но все равно делает, когда говоришь собрать ни в какую. Ремень не помогает, сколько не наказывай - никак не влияет. Нет сил моральных наказывать ее больше, сердце болит когда наказываешь. Библия говорит о розге, но что делать если она не помогает. Как наказывать малыша, который ужасно упрям? Или есть другой способ заставить ребенка понять что так делать нельзя. Спасибо.»",
            answer: "Здравствуйте. Если ремень не помогает(я думаю, что ремень может применяться лишь в исключительных случаях) то лучше прибегнуть к другим методам воспитания. Ребенку нужно больше вашего времени и участия в его занятиях. Скорее всего нужно с ним больше играть постепенно беря инициативу в выдумках игры на себя, но наблюдая, чтобы ребенку нравились игры, предлагаемые вами. Но это вовсе не значит, сто вам совсем не нужно прислушиваться к мнению дочери.\n\nВам необходимо больше постараться изучать характер и наклонности вашей дочери. Необходимо повысить ценность вашего участия в играх и занятиях, чтобы ребенок дорожил тем, что вы с ним играете и научился признавать ваш авторитет. Может быть нужно почти все представлять в виде игры, чтобы незаметно для себя ребенок научался вас слушаться. Уборку игрушек тоже можно осуществить в виде совместной игры, где ребенок уберет вначале одну- две игрушки, а вы остальные. Важно при этом подчеркнуть, что папе или маме будет очень приятно, что игрушки собраны, при этом договориться, чтобы папа или мама обязательно похвалил ребенка и даже неоднократно и того, кто с ним убирал. С течением времени при вашем терпении, любви и снисходительности будет наоборот: ребенок будет проявлять усердие и уберет больше, чем вы.\n\nПри этом важно с ребенком много общаться. Разговаривать, выслушивать. Нужно ему много читать детских стихов, сказок, рассказиков, где бы работало его воображение, которое нуждается в развитии. И без вашего общения это развитие невозможно. Ни через мультфильмы и телевизор, только через общение с живыми людьми достигается развитие образного мышления и речи у детей. Это не значит, что мультфильмы совершенно запрещены. Через идеи детских мультфильмов тоже происходит научение. Но использование мультфильмов должно быть четко регламентировано. Кстати детям очень нравятся \"мультики\" и это тоже можно использовать в процессе воспитания, как для наказания, так и для поощрения.\n\nКстати поощрение играет очень большую роль в воспитании. Думается, что гораздо важнее придумывать мотивации для послушания, чем наказывать за проступки. Видимо ремень надо отложить в сторону и не прибегать к нему. Это будет вызывать ожесточение в душе ребенка и калечить его личность. Еще раз хочется подчеркнуть важность родительского участия в занятиях ребенка. Это не заменяется и не восполняется ничем. Обучение дисциплине и послушанию может быть весьма разнообразным. Процесс этот очень не прост и требует индивидуального подхода к каждому ребенку. С нашей родительской стороны большой любви и нежности,терпения и строгости, мудрости и доброты, нашего времени и участия и, конечно, молитв Богу.",
            footer: "Желаем вам успехов и благословения от Бога.\nПресвитер церкви, Обровец П.Д."
        }
