            question: "«Хочу начать жизнь сначала. Устал от греха. Точнее отвернулся от него.Что мне делать дальше?»",
            answer: "Невозможно полностью ответить на ваш вопрос из-за недостатка информации. Совершенно неясно, как вы относитесь к Иисусу Христу, просили ли вы Бога о прощении ваших грехов, неясны и другие вопросы, связанные с вашим духовным состоянием. Но все-таки некоторые советы можно иметь, которые помогут вам идти в правильном направлении.\n1. Необходимо молиться. В молитве открывать свое сердце Богу и просить милости и спасения. Христос сказал ученикам. \"Бодрствуйте и молитесь, чтобы не впасть в искушение: дух бодр, плоть же немощна\". (Марк 14:38)\n2. Читать Библию. Хотя бы одну- две главы каждый день и размышлять о прочитанном. \"Исследуйте Писания, ибо вы думаете чрез них иметь жизнь вечную; а они свидетельствуют о Мне\".(Ин.5:39) \"Вникай в себя и в учение; занимайся сим постоянно: ибо, так поступая, и себя спасешь и слушающих тебя\".(1-е.Тим.4:16) 3. Найти собрание верующих(поместную церковь христиан), вера и учение которых соответствуют Библии (проверять нужно достаточно долго) и принимать участие в жизни этой церкви. Молитесь Богу, чтобы Он вам помог найти таких христиан. \"Итак охотно принявшие слово его крестились, и присоединилось в тот день душ около трех тысяч. И они постоянно пребывали в учении Апостолов, в общении и преломлении хлеба и в молитвах\". (Деяния 2:41-42)",
