            answer: "Библия не выделяет в своем откровении какую - то цель или задачу церкви, как главнейшую, потому что в жизни и устроении церкви все взаимосвязано, подобно как и в организме человека. Еф.4:11-16. В этом тексте Писания Апостол говорит о том, что Христос поставил в церкви служителей для того, чтобы святые достигали совершенства,чтобы духовно возрастали в \"возраст Христов\", достигали единства веры.\n\nПрощаясь с учениками Христос дает им поручение учить вере все народы, крестить уверовавших и учить их соблюдать все, что Он им заповедовал (Мф.28:19-20). В книге Деяния Апостолов Христос поручает ученикам быть Ему свидетелями (Д.1:8) В 1Тим.3:15 Апостол называет церковь Божью столпом и утверждением истины Это не полный перечень задач для церкви,упоминаемых в Библии. Как упрощенный вывод можно сказать, что у церкви есть задачи внутреннего устроения и задачи, решаемые церковью во внешнем мире.\n\nК задачам внутреннего устроения более всего относится текст Еф. 4:11-16, а к задачам служения церкви в этом мире больше относятся тексты: Мф.28:19-20; Д.1:8; 1Тим.3:15. Хотя это условно, так как все приведенные тексты говорят и о том и о другом. Упрощая, можно сказать, что к внутрицерковным задачам относится: достижение единства веры, и духовное возрастание верующих, достижение ими возраста Христова (но это далеко не полный список), а к внешним задачам относится благовестие, при этом церковь(она не названа истиной) должна быть опорой истине, выражать и утверждать истину. (\"Благодать же и Истина произошли чрез Иисуса Христа\", -Ин.1:17) Не достигая внутреннего устроения, церковь не сможет полноценно и правильно решать внешние задачи. Кроме задач, стоящих перед всей церковью, как телом Христовым есть еще и цели, которые христианину нужно достигать на личном уровне (Фил.3: 7-11-15); ( От.2-3 -\"Побеждающий\")",
            footer: "Господь Бог да благословит Вас.\nПресвитер церкви, Обровец П.Д."
